    height: 1000px;
    background: url('./img/circleBg.png') 50% 50% no-repeat; */
}
.panel-box{
    /* background: #f0f; */
    padding-top: 20px;
    box-sizing: border-box;
    overflow: hidden;
}
.panel-children{
    position: absolute;
    z-index: 1;
}
.panel-title{
    position: absolute;
    color: #7eddfb;
    top: 0px;
    font-size: 14px;
}
.panel-border-top{
    position: absolute;
    top: -20px;
    width: 100%;
    height: 80px;
    z-index: 0;
    background: url(/static/media/borderBgc.e9c61d17.png) no-repeat;
    background-size: 100% 100%; 
    -webkit-transform: rotateX(180deg); 
            transform: rotateX(180deg);
    /* background: linear-gradient(to right, rgba(13, 65, 251, 1), rgba(13, 65, 251, 1), rgba(13, 65, 251, 0) 50%, rgba(13, 65, 251, 0), rgba(13, 65, 251, 0), rgba(13, 65, 251, 0), rgba(13, 65, 251, 1) 70%); */
}
.panel-border-bottom{
    position: absolute;
    z-index: 0;
    bottom: -20px;
    width: 100%;
    height: 80px;
    background: url(/static/media/borderBgc.e9c61d17.png) no-repeat;
    background-size: 100% 100%; 
}

.menu-list{
    position: relative;
    /* top: -32px; */
    top: 0px;
    /* transition: width .4s !important; */
}
.menu-container{
    width: 200px;
    position: relative;
    top: 0px;
    float: left;
    height: 100%;
    background: #1f2c35;
    -webkit-transition: width .1s;
    transition: width .1s;
}
.router-container{
    float: left;
    position: relative;
    /* background: #0e0436; */
    background: url('/static/img/bg-4.png') no-repeat;
    background-size:100% 100%;
    height: 100%;
    width: 2800px;
    -webkit-transition: width .1s;
    transition: width .1s;
    color: #fff;
}
.toolbar{
    position: relative;
    left: 0px;
    height: 40px;
    background: #fff;
}
.button-toggle{
    position: absolute;
    left: 10px;
    top: 0px;
    z-index: 10;
}
ul.menu-List,.menu-List ul,.menu-List li,.menu-List li ul{
    position: relative;
    background: #1f2c35 !important;
}
.menu-container a,.menu-container li{
    color: #fff;
}
.menu-container a:focus{
    text-decoration: none !important;
}
.button-home{
    position: absolute !important;
    left: 66px;
    top: 0px;
    z-index: 10;
}
li.itemSelected {
    /* background: #1890ff !important; */
    color: #36a7df !important;
}
li.itemSelected a{
    color: #36a7df;
}
.ant-menu-item span{
    color: #fff;
}
li{ cursor: default !important;}
.App{
    width: 3000px;
    height: 1080px;
}
